export class CircleValue
{
  diameter: number;
  damage: number;
  circleTimeSeconds: number;
  shrinkTimeSeconds: number;

  constructor(circleJson)
  {
    this.diameter = circleJson.diameter;
    this.damage = circleJson.damage;
    this.circleTimeSeconds = circleJson.circleTime;
    this.shrinkTimeSeconds = circleJson.shrinkTime;
  }
}

export class CircleValues
{
  circles: CircleValue[] = [];

  constructor()
  {
    //Circle 0, plane drop
    this.circles.push(new CircleValue({
      diameter: 4550,
      damage: 0,
      circleTime: 110,
      shrinkTime: 0
    }));

    //Circle 1
    this.circles.push(new CircleValue({
      diameter: 4550,
      damage: .4,
      circleTime: 300,
      shrinkTime: 300
    }));

    //Circle 2
    this.circles.push(new CircleValue({
      diameter: 2970,
      damage: .6,
      circleTime: 200,
      shrinkTime: 145
    }));

    //Circle 3
    this.circles.push(new CircleValue({
      diameter: 1480,
      damage: .8,
      circleTime: 145,
      shrinkTime: 90
    }));

    //Circle 4
    this.circles.push(new CircleValue({
      diameter: 740,
      damage: 1,
      circleTime: 120,
      shrinkTime: 60
    }));

    //Circle 5
    this.circles.push(new CircleValue({
      diameter: 360,
      damage: 3,
      circleTime: 120,
      shrinkTime: 40
    }));

    //Circle 6
    this.circles.push(new CircleValue({
      diameter: 175,
      damage: 5,
      circleTime: 90,
      shrinkTime: 30
    }));

    //Circle 7
    this.circles.push(new CircleValue({
      diameter: 90,
      damage: 7,
      circleTime: 90,
      shrinkTime: 30
    }));

    //Circle 8
    this.circles.push(new CircleValue({
      diameter: 40,
      damage: 11,
      circleTime: 60,
      shrinkTime: 15
    }));

    //Round End
    this.circles.push(new CircleValue({
      diameter: 40,
      damage: 11,
      circleTime: 300,
      shrinkTime: 0
    }));
  }
}




