import { Component, OnInit } from '@angular/core';
import {GameClockService} from "../../services/game-clock.service";
import {CircleProgressService} from "../../services/circle-progress.service";
import {CircleValues} from "../../objects/CircleValues";
import {CircleProgressData} from "../../objects/CircleProgressData";

@Component({
  selector: 'app-game-clock-controller',
  templateUrl: './game-clock-controller.component.html',
  styleUrls: ['./game-clock-controller.component.css']
})
export class GameClockControllerComponent implements OnInit {

  private circleValues: CircleValues = new CircleValues();
  private circleProgressData: CircleProgressData = new CircleProgressData({
    percentage: 0,
    currentCircleIndex: 0,
    currentCircleTime: 0,
    shrinking: false
  });

  private manualGameClock = 0;

  constructor(private gameClockService: GameClockService,
              private circleProgressService: CircleProgressService) { }

  ngOnInit() {
  }

  startTimer()
  {
    this.gameClockService.startGameClock();
  }

  stopTimer()
  {
    this.gameClockService.stopGameClock();
  }

  resetTimer()
  {
    this.gameClockService.resetGameClock();
    this.circleProgressService.resetProgress();
  }

  setGameClock()
  {
    let gameClockBeforeCircle = 0;
    for (let i = 0; i < this.circleProgressData.currentCircleIndex; i++)
    {
      gameClockBeforeCircle += this.circleValues.circles[i].circleTimeSeconds;
      gameClockBeforeCircle += this.circleValues.circles[i].shrinkTimeSeconds;
    }
    if (this.circleProgressData.shrinking)
    {
      gameClockBeforeCircle += this.circleValues.circles[this.circleProgressData.currentCircleIndex].circleTimeSeconds;
    }

    this.gameClockService.setGameClock(gameClockBeforeCircle + this.manualGameClock);
  }
}



