import { Component, OnInit } from '@angular/core';
import {GameClockService} from "../../services/game-clock.service";

@Component({
  selector: 'app-game-clock',
  templateUrl: './game-clock.component.html',
  styleUrls: ['./game-clock.component.css']
})
export class GameClockComponent implements OnInit {

  private gameClockSeconds = 0;
  constructor(private gameClockService: GameClockService) { }

  ngOnInit() {
    this.gameClockService.gameClockInSeconds$
      .subscribe(gameClock => this.gameClockSeconds = gameClock);
  }

  increaseGameClock()
  {
    this.gameClockService.increaseGameClock();
  }

  decreaseGameClock()
  {
    this.gameClockService.decreaseGameClock();
  }

  formatSeconds(totalSeconds: number)
  {
    let minutes = Math.floor(totalSeconds / 60);
    let seconds = totalSeconds - (minutes * 60);
    let secondsString: string = seconds.toString();
    if (seconds < 10)
    {
      secondsString = "0" + seconds;
    }
    return minutes + ":" + secondsString;
  }

}



