import {Component, OnInit} from '@angular/core';
import {CircleValues} from "../../objects/CircleValues";
import {CircleProgressData} from "../../objects/CircleProgressData";
import {CircleProgressService} from "../../services/circle-progress.service";

@Component({
  selector: 'app-progress-spinner',
  templateUrl: './progress-spinner.component.html',
  styleUrls: ['./progress-spinner.component.css']
})
export class ProgressSpinnerComponent implements OnInit
{
  private circleValues: CircleValues = new CircleValues();
  private circleProgressData: CircleProgressData = new CircleProgressData({
    percentage: 0,
    currentCircleIndex: 0,
    currentCircleTime: 0,
    shrinking: false
  });

  private circleColor = "primary";
  private flashClosing: boolean = false;
  private flashingToken;

  constructor(private circleProgressService: CircleProgressService)
  {
  }

  ngOnInit()
  {
    this.circleProgressService.circleProgressData$
      .subscribe(newProgressData =>
      {
        this.circleProgressData = newProgressData.getCopy();
        this.setPercentage();
      });
    this.flashingToken = setInterval(() =>
    {
      this.flashClosing = !this.flashClosing;
    }, 400);
  }

  setPercentage()
  {
    if (this.circleProgressData.percentage < 75)
    {
      this.circleColor = "primary";
    }
    else if (this.circleProgressData.percentage >= 75 && this.circleProgressData.percentage < 90)
    {
      this.circleColor = "accent";
    }
    else if (this.circleProgressData.percentage >= 90)
    {
      this.circleColor = "warn";
    }
  }
}



