import { Injectable } from '@angular/core';
import {Subject} from "rxjs/Subject";

@Injectable()
export class GameClockService {

  private timerToken;
  private gameClockValue: number = 0;

  private gameClockInSeconds = new Subject<number>();
  public gameClockInSeconds$ = this.gameClockInSeconds.asObservable();

  constructor() { }

  public startGameClock()
  {
    if (this.timerToken == null)
    {
      this.timerToken = setInterval(() => {
        this.gameClockValue += 1;
        this.gameClockInSeconds.next(this.gameClockValue);
      }, 1000);
    }
  }

  public stopGameClock()
  {
    clearInterval(this.timerToken);
    this.timerToken = null;
  }

  public resetGameClock()
  {
    this.stopGameClock();
    this.gameClockValue = 0;
    this.gameClockInSeconds.next(0);
  }

  public setGameClock(gameClockValue: number)
  {
    this.gameClockValue = gameClockValue;
    this.gameClockInSeconds.next(this.gameClockValue);
  }

  public increaseGameClock()
  {
    this.gameClockValue += 1;
    this.gameClockInSeconds.next(this.gameClockValue);
  }

  public decreaseGameClock()
  {
    this.gameClockValue -= 1;
    this.gameClockInSeconds.next(this.gameClockValue);
  }
}



