import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';


import { AppComponent } from './app.component';
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {
  MatButtonModule, MatCardModule, MatChipsModule, MatGridListModule, MatIconModule, MatInputModule,
  MatProgressSpinnerModule
} from "@angular/material";
import {FlexLayoutModule} from "@angular/flex-layout";
import { ProgressSpinnerComponent } from './components/progress-spinner/progress-spinner.component';
import {GameClockService} from "./services/game-clock.service";
import {FormsModule} from "@angular/forms";
import { GameClockControllerComponent } from './components/game-clock-controller/game-clock-controller.component';
import { GameStatsComponent } from './components/game-stats/game-stats.component';
import {CircleProgressService} from "./services/circle-progress.service";
import { GameClockComponent } from './components/game-clock/game-clock.component';


@NgModule({
  declarations: [
    AppComponent,
    ProgressSpinnerComponent,
    GameClockControllerComponent,
    GameStatsComponent,
    GameClockComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FlexLayoutModule,
    FormsModule,

    // Material modules
    MatButtonModule,
    MatProgressSpinnerModule,
    MatChipsModule,
    MatInputModule,
    MatGridListModule,
    MatIconModule,
    MatCardModule,
  ],
  providers: [GameClockService, CircleProgressService],
  bootstrap: [AppComponent]
})
export class AppModule { }



